<template>
  <div @click.prevent="selectUsers">
    <span
      class="avatar rounded-circle tk-layout-inline-block tk-border-color-white tk-text-center text-lg tk-position-relative tk-bg-color-card8 tk-font-color-white"
      :class="{
        'avatar-sm text-xs': avatar === 'sm',
        'avatar-lg text-xxl': avatar === 'lg',
        'avatar-xl text-xxl': avatar === 'xl',
      }"
    >
      <img
        :src="data.avatar"
        v-if="!isNull(data.avatar) && imgIsValid"
        @error="imgIsValid = false"
        class="tk-position-absolute"
        style="right: 0"
      />
      <span
        v-else
        :style="{ lineHeight: getAvatarLineHeight(avatar) + 'px' }"
        >{{ getFirstLetter(data.name) }}</span
      >
    </span>
    <span
      class="tk-layout-inline-block tk-px-margin-left-px20"
      :style="{
        width:
          toolTip || selectUser || checkUser
            ? 'calc(100% - 20px - ' + avatarWidth + 'px - 24px)'
            : 'calc(100% - 20px - ' + avatarWidth + 'px)',
      }"
    >
      <div class="tk-font-weight-bold">{{ data.name }}</div>
      <div
        class="tk-font-color-gray"
        :class="{
          'tk-layout-inline-block tk-px-margin-right-px24': inline,
          'tk-text-overflow': !inline,
        }"
        :title="data.email"
        v-if="!isNull(data.email)"
      >
        <i class="fas fa-envelope tk-layout-inline-block"></i>
        <span class="tk-layout-inline-block tk-px-margin-left-px10">{{
          data.email
        }}</span>
      </div>
      <div
        class="tk-font-color-gray"
        :class="{
          'tk-layout-inline-block tk-px-margin-right-px24': inline,
          'tk-text-overflow': !inline,
        }"
        :title="data.phone"
        v-if="!isNull(data.phone)"
      >
        <i class="fas fa-phone tk-layout-inline-block"></i>
        <span class="tk-layout-inline-block tk-px-margin-left-px10">{{
          data.phone
        }}</span>
      </div>
      <div
        class="tk-font-color-gray tk-cursor-pointer"
        :class="{
          'tk-layout-inline-block  tk-px-margin-right-px24': inline,
          'tk-text-overflow': !inline,
        }"
        :title="data.website"
        v-if="!isNull(data.website)"
        @click.prevent="openWebsite(data)"
      >
        <i class="fas fa-link tk-layout-inline-block"></i>
        <span class="tk-layout-inline-block tk-px-margin-left-px10">{{
          data.website
        }}</span>
      </div>
    </span>
    <span
      class="tk-layout-inline-block tk-px-width-px20 tk-text-right tk-text-base"
      v-if="toolTip || selectUser || checkUser"
    >
      <!-- <i class="fas fa-ellipsis-v"></i> -->
      <el-dropdown trigger="hover" class="dropdown" v-if="toolTip">
        <span
          class="btn btn-sm btn-icon-only text-light tk-font-color-gray lh-200"
        >
          <i class="fas fa-ellipsis-v"></i>
        </span>
        <el-dropdown-menu
          class="dropdown-menu dropdown-menu-arrow show"
          slot="dropdown"
        >
          <span
            class="dropdown-item tk-cursor-pointer"
            @click.prevent="onEdit"
            v-if="showEdit"
          >
            <i
              class="fas fa-user-edit tk-layout-inline-block tk-px-width-px16 tk-font-color-gray"
            ></i>
            <span class="tk-layout-inline-block">Edit</span>
          </span>
          <template v-if="showArchive">
            <span
              class="dropdown-item tk-cursor-pointer"
              v-if="!data.archive"
              @click.prevent="onArchive"
            >
              <i
                class="fas fa-inbox tk-layout-inline-block tk-px-width-px16 tk-font-color-gray"
              ></i>
              <span class="tk-layout-inline-block">Archive</span>
            </span>
            <span
              class="dropdown-item tk-cursor-pointer"
              v-else
              @click.prevent="onActive"
            >
              <i
                class="fas fa-undo-alt tk-layout-inline-block tk-px-width-px16 tk-font-color-gray"
              ></i>
              <span class="tk-layout-inline-block">Active</span>
            </span>
          </template>
          <span
            class="dropdown-item tk-cursor-pointer"
            @click.prevent="onDelete"
          >
            <i
              class="fas fa-trash tk-layout-inline-block tk-px-width-px16 tk-font-color-gray"
            ></i>
            <span class="tk-layout-inline-block">Delete</span>
          </span>
        </el-dropdown-menu>
      </el-dropdown>
      <span class="tk-font-color-lightGray lh-200" v-if="selectUser">
        <i class="fas fa-angle-right"></i>
      </span>
      <span class="tk-font-color-main lh-200" v-if="checkUser">
        <i class="fas fa-check" v-if="checked"></i>
      </span>
    </span>
  </div>
</template>

<script>
import $ from "jquery";
import { DropdownMenu, DropdownItem, Dropdown } from "element-ui";
export default {
  props: {
    data: {
      default() {
        return {};
      },
    },
    compareData: {
      default() {
        return {};
      },
    },
    avatarWidth: {
      default: 80,
    },
    avatar: {
      type: String,
      default: "xl",
    },
    inline: {
      type: Boolean,
      default: false,
    },
    toolTip: {
      type: Boolean,
      default: false,
    },
    selectUser: {
      type: Boolean,
      default: false,
    },
    checkUser: {
      type: Boolean,
      default: false,
    },
    showArchive: {
      type: Boolean,
      defatul: true,
    },
    showEdit: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      checked: false,
      imgIsValid: true,
    };
  },
  components: {
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
  },
  created() {
    let self = this;
    self.compare();
  },
  methods: {
    openWebsite(data) {
      window.open(data.website, "_blank");
    },
    compare() {
      let self = this;
      if (self.checkUser) {
        if (
          self.data.name === self.compareData.name &&
          self.data.email === self.compareData.email
        ) {
          self.checked = true;
        }
      }
    },
    selectUsers() {
      let self = this;

      if (self.checkUser) {
        self.$emit("selectUsers", this.data);
      } else {
        self.$emit("selectUsers");
      }
    },
    hideDropdownMenu() {
      $(".dropdown-menu").slideUp();
    },
    onEdit() {
      this.$emit("onEdit", this.data);
      this.hideDropdownMenu();
    },
    onArchive() {
      this.$emit("onArchive", this.data);
      this.hideDropdownMenu();
    },
    onDelete() {
      this.$emit("onDelete", this.data);
      this.hideDropdownMenu();
    },
    onDeleteArchive() {
      this.$emit("onDeleteArchive", this.data);
      this.hideDropdownMenu();
    },
    onActive() {
      this.$emit("onActive", this.data);
      this.hideDropdownMenu();
    },
    isNull(data) {
      return this.$tools.isNull(data);
    },
    getFirstLetter(str) {
      return this.$tools.getFirstLetter(str);
    },
    getAvatarLineHeight(avatar) {
      return this.$tools.getAvatarLineHeight(avatar);
    },
  },
};
</script>